          <h1>《项目工场用户服务协议》</h1>
          <h2>第一条  定义及解释</h2>
          <p>1.1、除本协议另有规定外，下列用语或术语应当具有以下定义：</p>
          <p>(1).清科顾问指清科管理顾问集团有限公司，项目工场指清科顾问运营的为用户（包括但不限于融资方、投资方、财务顾问）提供商业计划书（BP）指导、对接信息、组织路演等一系列围绕投融资服务的互联网平台。</p>
          <p>(2).用户指符合清科顾问及项目工场规定的条件、同意遵守本协议的约定、自愿申报注册成为项目工场平台认证用户并被许可使用项目工场平台服务的主体，用户包括融资方、投资方、财务顾问及其他认证用户。</p>
          <p>(3).融资方指符合清科顾问及项目工场规定的创建项目相关资格要求、同意遵守本协议的约定自愿申请并经项目工场审核通过，使用项目工场创建项目服务的用户。</p>
          <p>(4).投资方指符合清科顾问及项目工场规定的创建合格投资人相关资格要求，同意遵守本协议的约定自愿申请并经项目工场审核通过，使用项目工场寻找投资项目标的的用户。</p>
          <p>(5).财务顾问指符合清科顾问及项目工场规定的对接投融资信息相关资格要求、同意遵守本协议的约定自愿申请并经项目工场审核通过，使用项目工场将项目对接于平台中的投资方，或是将资金对接于平台中的融资方的用户。</p>
          <p>(6).平台服务指清科顾问及项目工场为用户提供的一系列信息对接服务、平台软件系统支持等服务，包括但不限于商业计划书（BP）指导、发布/浏览信息服务、创建项目服务（是指项目工场为融资方、财务顾问提供创建融资项目的相关服务）、线上/线下路演服务、在线投资/融资服务等。</p>
          <p>1.2、本协议提及的条款，均是指本协议的条款。</p>

          <h2>第二条  协议当事人</h2>
          <p>2.1、清科顾问与用户经友好协商，依据《中华人民共和国合同法》、《中华人民共和国公司法》及其他相关法律、法规的规定，本着平等、自愿、等价有偿的原则，就项目工场向用户提供平台服务、创建项目服务、在线投资服务事宜，达成本服务协议，以资共同遵守。</p>
          <p>2.2、除非协议中另有约定，清科顾问、用户、融资方、投资方、财务顾问在本协议中合称为“各方”，单称为“一方”。</p>

          <h2>第三条  服务内容及价款</h2>
          <p>3.1、在阅读、理解本协议内容并同意接受本协议后，自愿申报注册成为项目工场平台用户的，在本协议生效后有权依据清科顾问及项目工场的相关规定及本协议的约定使用项目工场平台服务：</p>
          <p class="fBlod">☆基础服务（基础服务不需用户支付费用）：</p>
          <p>(1).创建融资项目；</p>
          <p>(2).浏览投融资信息；</p>
          <p>(3).免费有限投递次数，免费有限发起聊天次数；</p>
          <p>(4).免费浏览获取头条、研报资讯信息；</p>
          <p>(5).免费参加部分路演活动（以具体活动安排为准）；</p>
          <p>(6).免费查看平台认证投资人基本信息；</p>
          <p class="fBlod">☆会员服务（会员服务将自用户支付相应费用后方可开通）：</p>
          <p>(1).会员套餐服务：</p>
