          <p>A.线上BP投递：套餐有效期内在线投递BP，并保证可投递的投资人为已认证的真实有效投资人。</p>
          <p>B.投资人精准推荐：根据项目的领域、轮次，精准推荐投资领域相匹配、活跃度高且职位等级较高的投资人名录。</p>
          <p>C.无限联系投资人：平台已认证投资人库资源可供随时在线沟通，消息即时推送给投资人，在套餐有效期内，可无限次与平台任意已认证的投资人在线沟通。</p>
          <p>D.项目情况报告：实时查看展示项目的被访问情况数据，包括但不限于投资人浏览数据、投资人查看BP、投资人关注、投资人约见项目的数据等。</p>
          <p>E.沙丘大学“创业学院”会员课程：是围绕创业者“人+事+钱+策”四大核心经营管理主题的深度干货课程，同时为初创企业与投资人提供对接平台。购买会员套餐后在有效期内可免费在线获取沙丘大学“创业学院的会员课程”。</p>
          <p class="fBlod">用户购买“在线会员套餐服务”的，应知悉：</p>
          <p>A.套餐到期后套餐内未使用的权限失效。</p>
          <p>B.套餐购买后：</p>
          <p>(1).iOS端用户可升级，升级只需补升级差价，套餐权益以升级后的套餐为准。</p>
          <p>(2).安卓端用户，可分别单独购买不同等级会员套餐，也可升级购买。</p>
          <p>C.套餐内容购买后不支持退款、转让。</p>
          <p>D.升级后剩余权益=升级的套餐的总权益-升级前已用掉的权益。</p>
          <p>E.升级后有效期的开始日期为升级过程中第一个套餐的开始日期，结束日期为第一个套餐开始日期+升级后套餐的有效期。</p>
          <p class="fBlod">(2).会员单项线下精品服务：</p>
