          <p>A.BP指导：给需要BP指导的创业者提供商业模式梳理、优化表达语句，美化设计。</p>
          <p>B.投资人点评：根据项目领域及融资轮次，精准推荐一位专业投资人点评融资项目，找出项目可以优化的地方并给出合理建议。</p>
          <p>C.首页轮播广告位：项目在平台banner广告位置顶。</p>
          <p>D.项目置顶：人工把项目在平台投资人首页项目列表置顶。</p>
          <p>E.路演席位：根据项目轮次、领域、地区推荐适合的路演活动或按照项目方需求组织路演，包括但不限于线上&线下路演形式，时限为收到用户付费之日起6个月内推荐或组织。如6个月内仍无法成功组织路演的，每个项目的路演服务将更换为【项目工场APP首页10天项目置顶服务 + 项目工场一年“超值组合包”会员】或申请全额退款（退款周期以项目工场实际退款流程周期为准）。如由于用户单方原因拒绝参与路演活动或是未及时参与的，该费用将不予退还。</p>
          <p>3.2、融资方在本协议生效后有权依据清科顾问及项目工场的相关规定及本协议的约定使用项目工场平台提供的基础服务，如需会员服务，用户应另行支付费用。</p>
          <p>3.3、投资方在本协议生效后有权依据清科顾问及项目工场的相关规定及本协议的约定使用项目工场平台提供的基础服务，如需会员服务，用户应另行支付费用。</p>
          <p>3.4、财务顾问在本协议生效后有权依据清科顾问及项目工场的相关规定及本协议的约定使用项目工场平台提供的基础服务，如需会员服务，用户应另行支付费用。</p>
          <p>3.5、清科顾问及项目工场依据本协议的约定，运用自身的操作系统通过国际互联网络为用户提供平台服务、创建项目服务、投资服务、搓合交易等服务。</p>
          <p>3.6、通过清科顾问及项目工场的服务（包括所有线上、线下服务），融资方与平台认证投资人或是线下对接投资人就融资事宜最终产生交易的，融资方需另行向清科顾问支付就本次交易总金额的1%作为服务费用（具体约定详见附件《财务顾问服务协议》）。如融资方已与清科顾问就本3.6条的相关事宜独立签署协议的，将以该独立协议约定为准；如融资方未与清科顾问就本3.6条相关事宜独立签署协议的，清科顾问仍有权按照本附件《财务顾问服务协议》主张相应权利。</p>

          <h2>第四条  服务期限与服务价款</h2>
          <p>4.1、本协议服务期限自本协议生效之日起至用户资格被终止之日或平台服务中所涉具体交易或事项完成之日止（二者中以后到时间为准）。</p>
          <p>4.2、用户在服务期限内使用清科顾问及项目工场提供的会员服务的，需另行支付相应服务价款，其支付成功之日即为享受会员服务之日，会员服务期限将与其购买的套餐一致。</p>
          <p>4.3、如用户欲购买会员服务并需纸质版协议的，清科顾问及项目工场将线下与其另行签署本协议及《购买服务确认函》。</p>

          <h2>第五条  清科顾问之权利和义务</h2>
          <p>5.1、清科顾问声明并承诺其是依据中国法律设立并有效存续的有限责任公司，具有一切必要的权利及能力订立及履行本协议项下的所有义务和责任；本协议一经生效即对其具有法律约束力。</p>
          <p>5.2、清科顾问承诺并保证运用其项目工场平台为用户提供本协议约定的服务，但不对以下事项作出担保或保证或承担任何责任：</p>
          <p>(1).用户利用项目工场平台发布的信息的真实性、准确性、完整性、合法性、及时性或有效性；</p>
          <p>(2).用户的投资收益、交易的达成等事项；</p>
          <p>(3).投资方的投资损失；（清科顾问及项目工场不参与投资决策，仅为融资方和投资方的信息对接平台）</p>
          <p>(4).除另有特殊说明外，清科顾问及项目工场不以口头、书面或其他任何形式为用户提供意见或信息。</p>
          <p>5.3、清科顾问承诺并保证依据自身既有条件和水平最大限度的维护项目工场平台的系统稳定性，但不就电讯系统或互联网的中断或无法运作、技术故障、计算机错误或病毒、信息损坏或丢失或由此而产生的其他任何非自身原因引起的问题向用户或任何第三方承担赔偿责任。</p>
          <p>5.4、清科顾问及项目工场承诺并保证严格尊重和维护用户的隐私，保守用户的秘密，除非经用户书面授权，清科顾问及项目工场承诺并保证不公开、修改或透露用户/融资方/投资方/财务顾问的基本资料、项目保密信息，亦不公开项目工场各项服务中的非公开内容，但以下情况除外：</p>
          <p>(1).根据中国法律、法规和规范性文件的强制性规定，或应有权机关要求，有义务提供用户在项目工场平台网页上发布的信息内容及其发布时间、互联网地址或者域名等信息；</p>
          <p>(2).为维护项目工场平台的知识产权和其他合法权益；</p>
          <p>(3).在紧急情况下为维护用户个人和社会大众的合法权益；</p>
          <p>(4).根据本协议及清科顾问及项目工场的相关规定或者项目工场平台认为必要的其他情况下。</p>
          <p>5.5、用户有如下行为之一的，清科顾问/项目工场平台有权单方终止本协议，立即停止对用户发布信息的技术支持和其他服务，并保存有关记录、删除含有有关信息的地址、目录或关闭服务器，必要时可依据中国法律、法规及规范性文件向国家有关机关报告。用户应承担全部法律责任，并赔偿清科顾问/项目工场及其他任何第三方由此受到的损失，包括合理的追索费用：</p>
          <p>(1).用户提供的信息/产品侵害任何第三方知识产权、财产权等合法权益，或可能对第三方造成不利影响或侵害；</p>
          <p>(2).用户利用项目工场平台提供之网络平台进行任何违法犯罪活动，或宣传散播法律法规禁止散播的信息，或实施其他损害项目工场平台利益的行为；</p>
          <p>(3).用户有违反本协议及清科顾问/项目工场制定的其他相关制度、规则的行为，经清科顾问/项目工场警告后在一定期限内仍未改正；</p>
          <p>(4).用户的行为严重干扰清科顾问/项目工场正常的服务活动。</p>

          <h2>第六条  用户之权利和义务</h2>
          <p>6.1、用户声明并承诺其是依据中国法律设立并有效存续的有限责任公司/股份有限公司/其他形式的组织或为具有完全民事行为能力的自然人，具有一切必要的权利及能力订立及履行本协议项下的所有义务和责任；本协议一经生效即对其具有法律约束力。</p>
          <p>6.2、用户有权依据本协议的约定使用清科顾问和项目工场平台提供的各项服务。</p>
          <p>6.3、用户承诺并保证遵守本协议以及其与清科顾问或其关联方签署的其他协议的约定，并遵守清科顾问及项目工场制定的其他相关制度、规则，维护清科顾问及项目工场的正常运作及其公信力，不实施有损于清科顾问及项目工场的行为，包括但不限于以下行为：</p>
          <p>(1).未经清科顾问书面许可，通过本协议约定方式之外的途径进入清科顾问或项目工场计算机信息网络系统或者使用本协议约定范围外的信息网络资源或服务；</p>
          <p>(2).未经清科顾问书面许可，对清科顾问或项目工场平台计算机信息网络功能进行删除、修改或者增加；</p>
          <p>(3).未经清科顾问书面许可，对清科顾问或项目工场平台计算机信息网络中存储、处理或者传输的数据和应用程序进行删除、修改或者增加；</p>
          <p>(4).故意制作、传播计算机病毒等破坏性程序；</p>
          <p>(5).其他危害计算机信息网络安全、妨碍清科顾问及项目工场正常运作、有损其公信力的行为。</p>
          <p>6.4、用户承诺并保证妥善保管其在项目工场平台的账号和密码，并对其账户中的所有活动和事件承担责任。用户有权随时修改其密码和图标，也可注销旧的帐户开立新帐户。用户若发现任何非法使用其帐号或存在安全漏洞的情况，应立即通知清科顾问或项目工场并采取措施以减少不必要的损失。</p>
          <p>6.5、用户承诺并保证按照本协议第4.2、4.3款的规定向清科顾问按时、足额支付服务价款。</p>
          <p>6.6、用户承诺并保证其提供或发布的所有信息（包括但不限于用户、融资方、投资方、财务顾问的基本资料、项目保密信息等）是真实、准确、完整的，符合中国各项法律、法规及规范性文件以及清科顾问和项目工场相关规则的规定，不存在虚假、误导性陈述或重大遗漏，并承诺及时对已发布的信息进行更新、维护和管理。</p>
          <p>6.7、用户承诺并保证其提供或发布的信息不存在以下情形：</p>
          <p>(1).违反中国各项法律、法规及规范性文件规定的；</p>
          <p>(2).危害国家安全、泄露国家秘密、颠覆国家政权、破坏国家统一的，煽动民族仇恨、民族歧视，破坏民族团结的，破坏国家宗教政策、宣扬邪教和封建迷信的，有损国家荣誉和利益的；</p>
          <p>(3).散布谣言、含有淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪信息，扰乱社会秩序，破坏社会稳定的；</p>
          <p>(4).侮辱或者诽谤他人，侵犯其他任何第三方知识产权及其他合法权益的；</p>
          <p>(5).其他含有法律、法规及规范性文件禁止内容的。</p>
          <p>6.8、用户承诺并保证依据自身判断对在项目工场平台上创建、发布项目作出独立的、审慎的决定，并保证依法、依约完成其通过使用项目工场平台提供的服务而达成的各项交易。</p>
          <p>6.9、用户承诺并保证使用项目工场平台服务的风险由其自身承担，其根据自身需求下载或通过项目工场平台产品服务取得相关信息资料，并由其自行承担系统受损、资料丢失以及其它任何风险。</p>
          <p>6.10、用户承诺并保证若其违反上述承诺和保证而给清科顾问或项目工场造成损失，用户同意对清科顾问或项目工场因此而遭受的损失承担相应的赔偿责任。</p>

          <h2>第七条  融资方之权利和义务</h2>
          <p>7.1、融资方承诺并保证遵守本协议第六条的约定，享有相关权利，履行相关义务。</p>
          <p>7.2、融资方有权依据本协议的约定使用清科顾问和项目工场平台提供的创建项目服务。</p>
          <p>7.3、融资方承诺并保证按照本协议第4.2款的规定向清科顾问按时、足额支付服务价款。</p>

          <h2>第八条  投资方之权利和义务</h2>
          <p>8.1、投资方承诺并保证遵守本协议第六条的约定，享有相关权利，履行相关义务。</p>
          <p>8.2、投资方有权依据本协议的约定使用清科顾问和项目工场平台提供的在线投资服务。</p>
          <p>8.3、投资方承诺并保证按照本协议第4.2款的规定向清科顾问按时、足额支付服务价款。</p>
          <p>8.4、投资方承诺并保证依据自身判断对项目工场平台上发布的项目作出独立的投资决定、审慎发出投资意向；并保证依法、依约完成其通过使用项目工场平台提供的服务而达成的各项交易。</p>
          <p>8.5、投资方承诺并保证其已充分了解并知晓投资项目的风险，其使用项目工场平台服务的风险以及根据自身判断作出投资决定所造成的风险由其自行承担。</p>
          <p>8.6、投资方承诺并保证优先使用清科顾问提供的财务顾问服务或使用由清科顾问推荐的第三方为其提供财务顾问服务，具体的财务顾问服务内容由投资方与清科顾问或投资方与其他第三方另行协商签订服务协议进行约定。</p>

          <h2>第九条  财务顾问之权利和义务</h2>
          <p>9.1、财务顾问承诺并保证遵守本协议第六条的约定，享有相关权利，履行相关义务。</p>
          <p>9.2、财务顾问有权依据本协议的约定使用清科顾问和项目工场平台提供的平台服务。</p>
          <p>9.3、财务顾问承诺并保证按照本协议第4.2款的规定向清科顾问按时、足额支付服务价款。</p>
          <p>9.4、财务顾问承诺并保证其已充分了解并知晓投资项目的风险，其使用项目工场平台服务的风险以及根据自身判断作出投资决定所造成的风险由其自行承担。</p>

          <h2>第十条  知识产权</h2>
          <p>10.1、项目工场平台所提供服务涉及到的其自身开发的产品的所有权以及软件的知识产权等归清科顾问所有。</p>
          <p>10.2、用户或用户代理的著作权人同意清科顾问及项目工场在本协议有效期内对用户所上载的原创作品或是用户得到著作权人授权转载的作品享有信息网络传播权，但该等作品的所有权、著作权仍归用户或原著作权人享有。</p>

          <h2>第十一条  保密义务</h2>
          <p>11.1、本协议任何一方对根据本协议获得的其他方的保密信息（包括但不限于清科顾问/项目工场的技术秘密、资源、用户的基本资料等保密信息）及本协议所涉保密事项承担同等保密义务，未经协议其他方许可不得擅自将有关信息、资料披露给第三方（根据法律要求履行必要的信息披露义务除外）；如因违反本保密义务给任一方造成经济损失的，应予以赔偿。</p>
          <p>11.2、本协议任何一方对于基于本协议从另一方获取的标有秘密或专有标记的信息或技术、商业或者管理等方面的有关知识，应当承担保密义务，在本协议有效期内或本协议履行完毕之后，任何一方不得利用这些知识或信息，不得向他人披露；如因违反本保密义务给其他方造成经济损失的，应予以赔偿。</p>
          <p>11.3、上述保密义务不适用于任何下列情况</p>
          <p>(1).在本协议签订当日或之后任何时间，并非由于接受方的原因而为公众所知的保密资料；</p>
          <p>(2).有关法律、行政法规、规范性文件和有关政府、行业主管部门规定须予披露时；</p>
          <p>(3).为执行本协议，各方可按需要合理地向其雇员、专业顾问或代理人披露适当的保密资料，各方须确保和促使其雇员、专业顾问或代理人遵守本协议约定的保密义务。</p>

          <h2>第十二条  法律适用和争议解决</h2>
          <p>12.1、本协议的制定、解释、及其在执行过程中出现的或与本协议有关的异议的解决，受中华人民共和国现行有效的法律、法规及规范性文件的约束。</p>
          <p>12.2、在本协议执行过程中，若出现与本协议有关的争议，协议各方应尽量本着友好协商的精神予以协商解决；若各方于三十日内协商不能解决或任意一方不同意协商解决的，则任何一方有权将争议提交北京仲裁委员会按照该会当时有效的仲裁规则在北京进行仲裁解决。</p>
          <p>12.3、在仲裁过程中，除有争议的事项外，本协议应继续执行。</p>

          <h2>第十三条  违约责任</h2>
          <p>对于本协议任何一方的任何违约行为，其他方有权以书面形式通知违约方要求补救。除非违约方在十日内采取及时、充分的补救措施，否则要求补救的守约方有权就其所受的损失要求违约方赔偿。</p>

          <h2>第十四条  不可抗力</h2>
          <p>14.1、本协议所指不可抗力系指:地震、风暴、严重水灾或其他自然灾害，瘟疫、战争、暴乱、敌对行动、公共骚乱、公共敌人的行为、政府或公共机关禁止，黑客攻击、政府管制或因第三方原因导致网络通讯瘫痪等任何一方无法预见、无法控制和无法避免的事件。</p>
          <p>14.2、若发生了不可抗力事件导致任何一方或各方不能履行其任何的契约性义务，该等义务应在不可抗力事件存在时暂停，而义务的履行期应自动按暂停期顺延而不加以处罚。</p>
          <p>14.3、遭遇不可抗力的一方或各方应在发生不可抗力事件后的十天内向其他方提供发生不可抗力和其持续期的适当证明，并应尽其最大努力终止不可抗力事件或减少其影响。</p>
          <p>14.4、发生不可抗力事件时，各方应立即磋商以寻求一个公平的解决方法，并应采用所有合理努力以减轻不可抗力的影响。</p>

          <h2>第十五条  协议的生效与终止</h2>
          <p>15.1、本协议自用户同意接受本协议全部条款之日起生效。本协议生效后，对各方均具有法律约束力。</p>
          <p>15.2、本协议自第4.1款所述服务期限届满之日起终止。</p>

          <h2>第十六条  附则</h2>
          <p>16.1、本协议取代了各方就服务事项之前所达成的任何口头协议、谅解或备忘录。本协议项下的条款和条件构成各方完整和有约束力的义务，协议各方就本协议未尽事宜经协商一致签署的补充协议或确认函，与本协议具有同等的法律效力。</p>
          <p>16.2、清科顾问可因情势变更等原因修改本协议的条款，如用户继续使用项目工场平台提供的服务，则视为已明知并同意遵守修改后的协议；如不同意，可提出终止协议的要求并注销其在项目工场平台的账号。</p>
          <p>16.3、除非取得清科顾问的书面同意，用户不得将其在本协议项下的全部或部分权利与义务转让给任何第三方。</p>
          <p>16.4、本协议部分条款无效、被撤销或者终止的，不影响其他条款的效力，其他条款仍然有效。</p>
          <p>16.5、任何一方未能或延迟行使和/或享受其根据本协议享有的权利和/或利益，不应视为对该等权利和/或利益的放弃，且对该等权利和/或利益的部分行使不应妨碍未来对此等权利和/或利益的行使。</p>

