          <p>协议双方确认在本协议下，双方仅限于财务顾问服务关系。不因此而产生任何诸如代理、分销以及合作经营等法律关系。</p>

          <h2>9．违约责任</h2>
          <p>9.1 本协议生效后，甲、乙双方均应诚实信用地履行约定。任何一方违反约定，给守约方造成经济损失的，违约方应向守约方就其遭受的实际经济损失进行赔偿。法律另有规定或者本协议另有约定的除外。</p>
          <p>9.2 协议双方同意，通过双方共同努力，使得甲方或者其关联公司成功获融资或者并购后，甲方应当按照本协议的约定支付财务顾问服务费，逾期支付，甲方则每日应按照应付未付金额向乙方支付0.5％的利息。若逾期30天，则构成违约，应按应付未付金额的[ 0.5 ]%向乙方支付违约金。</p>

          <h2>10．法律适用及争议的解决</h2>
          <p>本协议的订立、效力、解释、履行和争议的解决，均受中华人民共和国法律的管辖。本协议的修改及变更必须经甲乙双方协商同意并签署书面协议后方能生效。如果发生争执，双方应协商解决，协商未成的，任何一方有权将该争议提交北京仲裁委员会进行仲裁。</p>

          <h2>11．其他</h2>
          <p>11.1 本协议一式两份，协议双方各持一份，具有同等法律效力。</p>
          <p>11.2 本协议未尽事宜或后续有变更事宜，由甲、乙双方协商，以书面形式另行签署线下协议或是签订补充协议。补充协议为本协议不可分割的一部分，与本协议具有同等法律效力。</p>

          <h2></h2>
          <p></p>
        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemScrollView
  },
  created () {
    window.location.href = window.location.origin + '/static/app/agreement.html'
    this._setTitle('项目工场用户服务协议')
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .agree
    font-size: 0.24rem
    background-color: #ffffff
    .page-panel
      .deal
        padding: 0 0.3rem
        background-color: #ffffff
        .table-wrapper
          padding-bottom: 0.24rem
          table
           width: 100%
          th, td
           vertical-align: middle
           text-align: center
           font-size: 0.28rem
           line-height: 0.4rem
           color: $color-text-body
           padding: 0.2rem 0
           border: 1px solid rgba(0,0,0,0.65)
        h1
          padding: 0.64rem 0 0.2rem 0
          font-weight: bold
          font-size: 0.34rem
          color: $color-text-title
          text-align: center
          letter-spacing: -0.0082px
          line-height: 0.48rem
        h2
          padding: 0.28rem 0 0.24rem 0
          font-weight: bold
          font-size: 0.3rem
          color: $color-text-body
          letter-spacing: -0.003rem
          line-height: 0.4rem
        p
          padding-bottom: 0.24rem
          text-align: justify
          font-size: 0.28rem
          color: $color-text-body
          letter-spacing: -0.003rem
          line-height: 0.4rem
        .fBlod
          font-weight: bold
          color: $color-text-title
</style>



